.rooms_caroucsel__item {
  height: 500px;
  font-size: 25px;
  font-weight: bold;
  text-align: center;
}

.rooms_caroucsel__item_1 {
  background: #fff;
}

.rooms_caroucsel__item_2 {
  background: #1225c9;
}

.rooms_caroucsel__item_3 {
  background: #c912b1;
}

.rooms_caroucsel__item_4 {
  background: #12c983;
}

.rooms_caroucsel__item_5 {
  background: #c9b712;
}

.slider_button {
  mask-image: url("../../assets/images/arrow.svg");
  width: 40px;
  height: 40px;
  background-color: #f7eaeab6;
}

.slider_button__left {
  transform: rotate(-90deg);
}

.slider_button__right {
  transform: rotate(90deg);
}
